import React from 'react';

export default class HomeBackground extends React.Component {
  constructor (props) {
    super(props);

    this.state = {};
  }

  render () {
    return (
      <div id="herobanner">
        <div className="stars"></div>
        <div className="twinkle"></div>
        <div id="herotext">
          <div>- Jason Diu -</div><br />
          Software Imagineer
        </div>
      </div>
    );
  }
}



