import React from 'react';
import {Link} from 'react-router-dom';

export default class NavItem extends React.Component {
  constructor (props) {
    super(props);
    this.state = {
      title: props.title,
      url: props.title.toLowerCase()
    }
  }

  render () {
    var selectedClass = window.location.pathname === '/' + this.state.url ? 'selected' : '';

    return (
      <li className="menuitem_ ">
        <Link
          to={`/${this.state.url}`} 
          onClick={() => {this.navItemClick();}}
          className={"menuitem " + selectedClass}>{this.state.title}</Link>
      </li>
    );
  }

  navItemClick = () => {
    this.props.setCurrentSection(this.state.url);
    if(this.props.toggleMobileNav){
      this.props.toggleMobileNav();
    }
  }
}


